.categories {
    padding-bottom: 3rem;
}

.categories__item {
    display: inline-block;
    background-color: #f2f2f2;
    font-size: 2rem;
    padding: 1rem 2rem;
    border-radius:  2rem;
    transition: all 0.1s ease;
    cursor: pointer;
}
.categories__item:not(:last-child) {
    margin-right: 1.5rem;
}
.categories__item:hover {
    background-color: #dfdfdf;
    transform: scale(1.1);
}
.categories__item_active {
    background-color: #ca5252;
    color: white;
    font-weight: 600;
}
.categories__item_active:hover {
    background-color: #ca5252;
    color: white;
    font-weight: 600;
}