.product {
    position: relative;
    display: flex;
    flex: 1 0 29%;
    flex-direction: column;
    background-color: #f5f5f5;
    border-radius: 1rem 1rem 0 0;
    padding: 1rem 1rem 2rem;
    transition: all 0.3s ease;
    min-width: 38.3rem;
    max-width: 38.3rem;

}
.product:hover {
    transform: scale(1.01);
}

.product__img {
    border-radius: 1.5rem 1.5rem 0 0;
    max-width: 100%;
}

.product__desc-box {
    padding: 1.5rem;
}

.product__title {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
}

.product__desc {
    font-size: 1.6rem;
    margin-bottom: 3rem;
}

.product__price {
    position: absolute;
    left: 2.5rem;
    bottom: 2.5rem;
    font-size: 2rem;
    font-weight: 600;
}

.product__plus {
    position: absolute;
    right: 2rem;
    bottom: 2rem;
    font-size: 2rem;
    width: 3.5rem;
    height: 3.5rem;
    background-color: #ca5252;
    color: white;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-weight: 600;
    transition: all 0.2s ease;
}
.product__plus:hover {
    transform: scale(1.3);
}