header {
    position: relative;
}

header .logo {
    font-weight: 600;
    font-size: 2.6rem;
    margin-right: auto;
}

/* Иконка корзины */
header .icon-box {
    padding-right: 3rem;
}
header .shop-cart-btn {
    cursor: pointer;
    height: 3rem;
    width: 3rem;
    transition: all 0.1s ease;
}
header .shop-cart-btn:hover,
header .shop-cart-btn_active {
    color: #ca5252;
    transform: scale(1.1);
}

/* Иконка корзины */

.header-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header .presentation {
    padding: 5rem 0;
    position: relative;
}
header .presentation::after {
    content: 'Лучшие товары для вашего дома';
    position: absolute;
    z-index: 10;
    left: 7rem;
    top: 14rem;
    font-size: 3.2rem;
    font-weight: 600;
    color: white;
}
header .presentation::before {
    content: 'по низким ценам';
    position: absolute;
    z-index: 10;
    left: 7.5rem;
    top: 18rem;
    font-size: 2.5rem;
    font-weight: 600;
    color: white;
}


header .presentation__img-box {
    position: relative;
}

header .presentation__filter {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #bcbcbc57;
}

header .presentation__img {
    background-size: cover;
    width: 100%;
}
