header .shop-cart {
    position: absolute;
    z-index: 20;
    background-color: #f5f5f5;
    /* transform: translate(-50%, -50%);
    top: 50%;
    left: 50%; */
    right: 0;
    top: 8.2rem;
    width: 50rem;
    padding: 3rem;
    border-radius: 0 0 20px 20px;

    -webkit-box-shadow: 4px 4px 50px 0px rgba(96, 96, 96, 0.2);
    -moz-box-shadow: 4px 4px 50px 0px rgba(96, 96, 96, 0.5);
    box-shadow: 4px 4px 50px 0px rgba(96, 96, 96, 0.5);
}

header .empty-cart {
    font-size: 3rem;
    font-weight: 600;
    text-align: center;
}

header .shop-cart__total {
    font-size: 2.3rem;
    font-weight: 600;
}