header .order {
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
}
header .order:not(:last-child) {
    margin-bottom: 3rem;
}

header .order__img {
    width: 10rem;
    margin-right: 3rem;
}

header .order__desc-box {
    margin-right: 5rem;
}

header .order__title {
    font-size: 3rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

header .order__price {
    font-size: 2rem;
    font-weight: 600;
}

header .order__trash {
    position: absolute;
    right: 0;
    bottom: 38%;
    width: 2.5rem;
    height: 2.5rem;
    color: #ca5252;
    cursor: pointer;
    transition: all 0.1s ease;
}
header .order__trash:hover {
    transform: scale(1.2);
}